@import "_mixins.scss";

.menu_logo {
  margin-top: 5px;


}

.menu_logo img {

  animation-name: logoAnimate;
  animation-duration: 1.5s;
  animation-timing-function: ease;
  animation-delay: 1.3s;
}

@keyframes logoAnimate {
  0% {
    margin-left: -30%;
  }

  50% {
    margin-right: -160%;
  }

  100% {
    margin-left: 30%;
  }
}

.menu-min {
  display: none;
}

.menu_top {
  display: flex;
  justify-content: space-around;
}

.menu_right .menu_ul>li:first-child {
  padding-left: 0;
}

.menu_left li,
.menu_right li {
  margin-top: 20px;
}

nav {
  height: 80px;
  font-weight: bold;

  & ul {
    padding: 0;
    margin: 0;
  }

  li {
    float: left;
    padding-left: 1em;
  }

  a {
    @include gray;
    padding-bottom: 5px;



    &:hover,
    &:focus,
    &:active {
      @include blueo;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        left: 25%;
        bottom: 0;
        height: 1px;
        width: 50%;
        border-bottom: 2px solid #5b6ceb;

      }
    }
  }
}

/// block form----------------------------
.fon_img {
  background: linear-gradient(rgba(79, 91, 196, 0.842), rgba(110, 112, 226, 0.575)), url(../img/fon-header.png) no-repeat center center;
  background-size: cover;
  height: auto;
}

.fon_box_container {
  display: flex;
  justify-content: center;
}

.fon_box {
  margin-right: 8%;
  margin-top: 8%;
  width: 300px;
  height: 391px;
  border-radius: 20px;
  box-shadow: 5.7px 5.7px 10px 0 rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
}

.fon_box h2 {
  @include blueo;
  margin-left: 33px;
}

.form-control {
  margin-left: 33px;
  margin-top: 17px;
  width: 230px;
  height: 50px;
  border-radius: 8px;
  border: none;
  background-color: #f2f2f2;
}

select {
  border: none;
  background-color: #f2f2f2;
  @include gray;
  padding: 12px 10px;


}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

.btn-menu {
  width: 170px;
  height: 50px;
  border-radius: 8px;
  box-shadow: 2.1px 2.1px 5px 0 rgba(0, 0, 0, 0.15);
  background-color: #5b6ceb;
  color: #fff;
  font-size: 16px;
  margin-left: 95px;
  cursor: pointer;

}

.btn-menu:hover {
  background-color: #b600ad;
  transition: .3s;
}

.fon_box_text {
  padding-top: 10%;
  padding-bottom: 10%;
  max-width: 500px;

  h1 {
    opacity: 0;
    font-family: 'Open Sans', sans-serif;
    font-size: 48px;
    font-weight: bold;
    line-height: 1.5;
    color: #ffffff;
    animation-name: boxText;
    animation-delay: 1.7s;
    animation-duration: 2s;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards;
  }

  p {
    font-family: 'Indie Flower', cursive;
    font-size: 25px;
    font-weight: normal;
    line-height: 2em;
    color: #ffffff;
    width: 280px;
  }
}

@keyframes boxText {
  0% {
    opacity: 0;
  }

  100% {

    opacity: 1;
  }
}