@import "_mixins.scss";


.program-title {
  padding-bottom: 76px;
  text-align: center;
}

.program {
  text-align: center;

  h2 {
    @include blueo;
    margin-top: 125px;
  }

  p {
    font-family: 'Indie Flower', cursive;
    @include gray;
    font-size: 25px;
  }
}

.program-big {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  margin-bottom: 120px;
  max-width: 1160px;
  // margin: 0 auto;

}

// .program-all{
