// }


.program-box {

  display: inline-block;

  max-width: 560px;
  height: 300px;
  border-radius: 8px;
  box-shadow: 3.5px 3.5px 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffffff;

  margin: 0px 20px 20px 0;
  overflow: hidden;

  & img {
    float: left;
    margin-right: 30px;

  }

  & h3 {
    text-align: left;
    @include gray;
    font-size: 20px;
    margin-top: 30px;

  }

  & p {
    text-align: left;
    @include gray-box;
    font-size: 14px;
    margin-bottom: 18px;
    line-height: 1.7;
  }

  .program-box-text-min {
    color: #999999;
  }
}

/*--------img-block------------*/

.img-block {

  line-height: 0;
  margin-bottom: 125px;
  text-align: center;

  & a {
    display: inline-block;
    width: 474px;
    height: 349px;
    position: relative;
    overflow: hidden;

  }


  & .descr {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(8, 68, 153, 0.84);
    color: #ffffff;
    padding-top: 150px;
    text-align: center;
    opacity: 0;
    transition: all .5s;
    font-size: 100px;
    
  }

  & a:hover .descr {
    top: 0;
    display: block;
    opacity: 1;
  }
}

