.about {
  display: inline-block;
  // flex-wrap: wrap;
  padding-left: 0 15px;
  // justify-content: space-around;
  text-align: center; // выровняем текст по центру блока
  margin: 0 auto;
}

.about-box-text {
  display: inline-block;
  flex-grow: 1;
  flex-basis: 360px;
  max-width: 400px;
  margin-top: 10px;
  padding-right: 50px;
  @include gray-box;

  & h2 {

    @include blueo;
  }
}

.oneslider {
  max-width: 35em;
  float: right;


}

.oneslider img {
  // max-width: 100%;
  height: auto;
}

.slickslider .about,      
.slick-prev,
.slick-arrow img {
  margin-left: 4px;
  width: 40px;
}

.slickslider .about,
.slick-next,
.slick-arrow img {
  margin-right: 26px;
  z-index: 1;
  width: 40px;
}


.slickslider,
.slick-prev:before,
.slick-next:before {
  display: none;

}